.App {
  text-align: left;
}
.App svg *{
  overflow: visible;}

.App-logo {
  height: 80px;
}

.App-header {
  background-color: #ededed;
  height: 30px;
  padding: 10px;
  color: #666;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
h1{
  padding: 0px;
  margin: 0px;
}
.settings{
  padding: 20px;
}
body,input,select,button{
  font-family: Titillium,sans-serif;
}
select{
  font-size: 18px;
}
.Event{
  font-weight: bold;
}
.atitle{
  font-weight: bold;;
  border-bottom: 1px solid #dedede;
  margin-bottom: 10px;
}
.rig{
  text-align: right;
}
.asubtitle{
  font-weight: bold;
  margin-top: 10px;
color:#666;
}
.alto{

}
.highRow td{
  background-color: yellow;

}
input,select,button{
  border: 1px solid #dedede;
  padding: 5px 10px;
  background-color: #ededed;
  margin: 5px 5px 5px 0px;
}
input[type=checkbox]{
  margin-left: 5px;
}
.action-inspector {
    position: absolute;
    left: 400px;
}
.players-viewer {
    position: absolute;
    left: 910px;
}
.distTD{
  text-align: right;
}

.teamtab {
    text-decoration: underline;
    margin-right: 20px;
    cursor: pointer;
}

.teamtab.active, .teamtab:hover {
    font-weight: bold;
}
.half{
  display: inline-block;
  width: 250px;
  text-align: center;
}
.linechart_area {
    color: red;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@font-face {
  font-family: Titillium;
font-weight:bolder;
    src: url(css/font/TitilliumWeb-Black.ttf);
}

@font-face {
  font-family: Titillium;
font-weight:bold;
    src: url(css/font/TitilliumWeb-Bold.ttf);
}

@font-face {
  font-family: Titillium;
font-weight:normal;
    src: url(css/font/TitilliumWeb-Regular.ttf);
}

@font-face {
  font-family: Titillium;
font-weight:lighter;
    src: url(css/font/TitilliumWeb-ExtraLight.ttf);
}



