.dot {
  border-radius: 100px;
  height: 30px;
  width: 30px;
  background-color: gray;
  position: absolute;


}

.field{
  background-color: green;
  background-image: url('./img/field.png');
  background-size: contain;
  position: absolute;
  left:20px;
}
.bolt{
  height: 12px;
  width: 12px;
  display: inline-block;
  background-image: url('./img/bolt.png');
  background-size: contain;

}

.fire{
  height: 12px;
  width: 12px;
  display: inline-block;
  background-image: url('./img/danger.png');
  background-size: contain;

}
.bigfire{
  height: 28px;
  width: 28px;
  display: inline-block;
  background-image: url('./img/danger_big.png');
  background-size: contain;
  background-position: -4px 1px;

}
.danger{
  color: #f59a06;
  font-weight: bold;
}
.velocity{
  color: #cc33ff;font-weight: bold;

}
.action-inspector{
  width: 500px;
}
.overo{
  overflow: visible;
}
.highlight circle{
  stroke-width: 3;
  stroke: yellow;
}
.resultDanger{
  font-size: 36px;
  color: #f59a06;

  display: block;
float:  right;
font-weight: lighter;
margin-top: -22px;

}
.danger .linechart_path{
  stroke-width: 3;
  stroke: #f59a06;
  fill:#ffffff;
}
.danger .linechart_area{

  fill: #f5e1c1;
}

.velocity .linechart_path{
  stroke-width: 3;
  stroke: #cc33ff;
  fill:#ffffff;
}
.velocity .linechart_area{

  fill: #f4d3ff;
}
.distance,.velocity,.danger{
  display: inline-block;

}
.resultDangerSmall{
    font-size: 14px;
    color:#999;
    padding-right: 5px;
    font-weight: normal;
    font-variant: small-caps;
}
table,td,tr{
  padding: 0px;
  margin: 0px;
}
td{

  padding: 2px 5px;
}
.fieldTop{
  padding-left:20px;
  padding-top: 0px;
}

.Person{
  text-shadow: 2px 2px 0px black;
}



